html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Space Grotesk", sans-serif;
  color: hsl(0deg, 0%, 85%);
  background-color: hsl(0deg, 0%, 8%);
}

ol,
ul {
  padding: 0;
  list-style: none;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1;
  font-weight: 700;
  color: hsl(0deg, 0%, 100%);
}

h1 {
  font-size: clamp(2.5rem, 1rem + 10vw, 5.5rem);
}

h2 {
  font-size: clamp(2.5rem, 1rem + 10vw, 5.5rem);
}

h3 {
  font-size: 1.5rem;
}

p {
  margin: 0;
  color: hsl(0deg, 0%, 85%);
}

a,
a:visited,
a:active {
  text-decoration: none;
}